h1{
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 64px;
  font-weight: bolder;
  color: #344FA199;

}
.idea-section, .qna-section{
  background: #EAECF8;
  
}

body{
  color:#8566FF;
}

.idea_column{
  padding: 0 6%;
  display: flex;
  flex-direction: row;
}
.ideas{
  text-align: center;
  padding: 2%;
  border-radius: 20px;
  margin-bottom: 2%;
}
.ideas .icon_ideas{
  text-align: center;
  padding: 5%;
}
.ideas .icon_ideas i.fa{
   font-size: 30px;
   color: white;
   background-color: #8566FF;;
   padding: 10%;
   border-radius: 42px;
}

p, .accordion-body{
  font-family: 'Rotunda', sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 300;
}
.accordion-button{
  font-family: 'AvertaStd-Regular', sans-serif;
  font-size: 20px;
  color: #8566FF;;
  font-weight: bold;
}
.nav-link{
  font-family: 'AvertaStd-Regular', sans-serif;
  color: #8566FF;;
  font-weight: bold;
}
.nav-item{
  margin-right: 5%;
}
ul.navbar-nav{
  margin-right: 5%;
}
h3{
  font-family: 'AvertaStd-Regular', sans-serif;
  font-size: 25px;
  font-weight: 700;
  padding: 1% 0 0.5% 0;
}
h2.heading{
  padding: 5% 0 5% 0;
  text-align: center;
  color: #8566FF;;
  font-size: 40px;
  line-height: 50px;
  font-weight: bold;
  font-family: 'Source Sans Pro', sans-serif;
}
.steps-section .col img{
  height: 300px;
}
.showmore{
  text-decoration: none;
  background-color: #8566FF;;
  border-radius: 25px;
  border-width: 0px;
  padding: 1% 2% 1% 2%;
  color: white;
  font-family: 'Source Sans Pro', sans-serif;
  display: flex;
  margin: 5% auto;
}
input.qna, textarea,input.qna_short{
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 15px;
  border-width: 0px;
  border-radius: 20px;
  margin-top: 2%;
  margin-bottom: 1%;
  padding: 1% 2% 1% 2%;
}

/* steps */

.right-sec col, .left-sec col{
  width: 50%;
}
.right-sec, .left-sec{
  margin: 0 0 5% 0;
}

.downloadbutton, .login{
  border-radius: 20px;
  font-family: 'Source Sans Pro', sans-serif;
  margin: 3% 5% 5% 0;
}
.downloadbutton{
  border-width: 2px;
  border-color: #8566FF;;
  color: #8566FF;;
}
.step_line{
  text-align: center;
  border-left: 5px solid #5A70B4;
  border-bottom-left-radius: 2px;
  height: 300px;
}
.step_line p{
  text-align: center;
  font-size: 20px;
  color:white;
  background-color: #5A70B4;
  width: 30%;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;

  padding: 8%;
}


 /* Login Registration */
.login{
  background: #8566FF;;
  color: white;
}
.downloadbutton:hover{
  background: #8566FF;;
  color: white;
}
.login_section{
  max-width: 600px;
  justify-content: center;
  background: #d1c5ffaf;
  border-radius: 35px;
}

.login_section input.qna{
  max-width: 400px;
  border-radius: 20px;
  border-width: 1px;
  border-color: grey;
}
.login_sec{
  text-align: center;
}

.login_sec h3{
  font-family:  'Source Sans Pro', sans-serif;
  font-size: 27px;
  padding: 1% 10%;
  color: #344FA1;
}

/* Hero Section */
.downloadqr{
  padding: 5% 0 5% 5%;
  background: white;
  border-radius: 36px;
}
.hero-section{
  background:  #EAECF8  ;
  margin: 5% 10% 5% 5%;
  border-radius: 40px;
  box-shadow: 2px 2px 20px 1px #344fa17d  ;
}


.iconsec{
  max-height: 75%;
  text-align: center;
  background: white;
  border-radius: 30px;
  margin: 5% 5% 0 2%;
  padding: 2% 1% 0 1%;
  box-shadow: 0.5px 0 5px 0px grey;
}
.iconsec .icon i.fa{
  padding: 20%;
  color: #8566FF;;
  font-size: 20px;
}
.infoqr .icon{
  background: white;
  padding: 3%;
}

.infoqr{
  display: flex;
  flex-direction: row;
}
.icon:hover{
  background: #8566FF;;
  border-radius: 25px;

}
.icon:hover i.fa{
  padding: 10%;
  margin: 10%;
  color: white;
}
.email{
  display: flex;
  flex-direction: row;
  margin: 5% 0 0 3%;
}
.email h3{
  font-size: 17px;
  font-weight: 300;
  font-family: 'Rotunda', sans-serif;
}
.email input, textarea.form-control{
  border-style: solid;
  margin-left: 8%;
  border-width: 2px;
  padding: 1%;
  border-radius: 10px;
  border-color: gray;
}
.generate{
   background: #8566FF;;
   color: white;
   border-radius: 25px;
}
.qna_short{
  width:100px;
}



.qr_content{

  margin: 5% 0 0 5%;
}
.dropdown .btn{
  color: #344FA1;
  max-width: 400px;
  background-color: white;
  border-radius: 20px;
  border-width: 4px;
  border-color: white;
  box-shadow: 0.5px 0.5px 5px 0.5px grey;
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 18px;
  margin: 3% 0;
}

.dropdown i{
  margin-left: 2px;
  color:#8566FF
}
.faq-section .accordion{
  width: 50% !important;
}
.modal-body{
  font-family: 'Source Sans Pro', sans-serif;
  text-align: center;
  color: #344FA1;
  ;
}
.modal-title{
  text-align: center;
}


.registration{
  text-decoration: none;
  border-radius: 20px;
  background: #D1C5FF3B;
  margin: 5%;
  padding: 5%;
}
.block{
  border-radius: 20px;
  background: white;
  box-shadow: 0 0 6px 0.5px grey;
  padding: 5%;
  margin: 5%;
}
.type-section{
  text-align: center;
}
.fileupload{

  border-radius: 20px;
}
.navbar-brand{
  margin-left: 10%;
}
.form-check{
  margin-left: 35%;
  text-align: left;
  padding-bottom: 2%;
  padding-top: 2%;
}
.form-check-input:checked{
  background-color: #8566FF;
  border-color:#8566FF ;
}


/* Footer */
.footer{
  margin-bottom: 7%;
}

.smicons, .footer .col img{
 
  margin: 0 0 10% 10%;
}
.smicons i.fa{
  color: #8566FF;;
  font-size: 35px !important;
  padding: 5%;
}

.qna-section h3{
  font-size: 18px;
  line-height: 24px;
}
.qna-section .row{
  margin-bottom: 3%;
}

/* Responsive  */
